export const db = firebase.firestore();
export const rdb = firebase.database();

let playerIndex = {}
export const getPlayers = gameKey => db.collection('games').doc(gameKey).collection('players').get().then(e => { e.forEach(doc => playerIndex[doc.id] = doc.data()); return playerIndex });

export const getTheme = gameKey => db.collection('games').doc(gameKey).collection('dashboard').doc('theme').get().then(doc => doc.data());

export const getGameState = gameKey => db.collection('games').doc(gameKey).collection('dashboard').doc('game').get().then(doc => doc.data());
