import React, { useState, useEffect } from 'react';
import styled from "styled-components"
import { Link, Route } from "react-router-dom"
import { getTheme, getGameState } from "../../database"
//components
import Lobby from "./components/lobby"
import AddPlayer from "./components/addPlayer"
import Dashboard from "./components/dashboard"

const Screen = styled.section`
    display:grid;
    grid-template-columns: 1fr;
    grid-template-rows: 75px 1fr;
    width: 100vw;
    height: 100vh;
    row-gap: 5px;
`
const Header = styled.section`
    justify-self:stretch;
    align-self:stretch;
    text-align:center;
    border-bottom: 1px #0000001c solid;
    background: ${({ color }) => color || "#121212"};
    display:grid;
    grid-template-columns: max-content 1fr max-content;
`
const Text = styled(Link)`
color: white;
margin:0;
padding:0;
font-size:20px;
text-align-center;
align-self:center;
justify-self:center;
padding: 0 5vw;
text-decoration:none;
transition:transform 200ms ease-in-out; 
&:focus,&:hover{
    transform:scale(0.99);
    transition:transform 200ms ease-in-out; 
}
`

const GameScreen = ({ gameId, route: { match: { params } } }) => {
    const [theme, setTheme] = useState({})
    const [state, setState] = useState({})
    useEffect(() => {
        getTheme(gameId).then(themeDoc => setTheme(themeDoc))
        getGameState(gameId).then(stateDoc => setState(stateDoc.playing))
    }, [gameId])
    console.log(state)

    return <Screen >
        <Header color={theme.color}>
            <Text to="/">Home</Text>
            <Text to="./"></Text>
            <Text to="./">Start de game</Text>
        </Header>

        {state ?
            <Dashboard gameId={gameId} /> :
            <Lobby color={theme.color} gameId={gameId} />}
        <Route path="/game/:gamekey/add"><AddPlayer gameId={gameId} /></Route>
    </Screen>
}
export default GameScreen;