import React from 'react';
import styled from "styled-components"
import { Link } from "react-router-dom"

const Screen = styled.section`
    display:grid;
    grid-template-columns: 1fr;
    grid-auto-flow:row;
    grid-auto-rows: max-content;
`
const Title = styled.h1`
text-align:center;
align-self:start;
`
const Button = styled(Link)`
    position:relative;
    text-align:center;
    align-self:center;
    padding: 20px 75px;
    text-decoration:none;
    outline:none;
    background: linear-gradient(180deg, rgba(18,18,18,1) 0%, rgba(0,0,0,1) 100%);
    border-radius: 10px;
    color:white;
    margin: 2.5px 0;

    &:focus,&:hover{
        transform:scale(0.99);
        transition:transform 200ms ease-in-out; 
    }
`
const Feed = styled.p`
    font-size: 18px;
    text-align:center;

`
const Credits = styled.a`
    text-decoration: none;
    font-weight: bold;
    color:black;
`

const IndexScreen = ({ }) => {
    return <Screen>
        <Title>Lasertag v1</Title>
        <Button to="/start/">Spelen</Button>
        <Button to="/doc/">Pistolen maken</Button>
        <Button to="/opensource/">Open Source</Button>
        <Feed>made by <Credits target=" " href="https://github.com/jobrav">Jochem</Credits> and <Credits target=" " href="https://www.instagram.com/lola_van_t_loo/">Lola</Credits></Feed>
    </Screen>
}
export default IndexScreen;