import React, { useState } from 'react';
import styled from "styled-components"
import { Link } from "react-router-dom"

const Screen = styled.section`
display:grid;
grid-template-columns: 1fr;
grid-auto-flow:row;
grid-auto-rows: max-content;
`
const Container = styled.div`
justify-self:stretch;
    align-self:start;
    display:grid;
    grid-template-columns: 1fr;
    padding: 0 25px;
    background: #fff;
    border-radius: 20px;
    box-shadow: 0 0 15px 5px #0000001c;
`
const Input = styled.input`
cursor:pointer;
    text-align:center;
    font-size: 4em;
    font-weight: bold;
    margin: 5px 0;
    padding: 25px 0;
    color:#000;
    outline: none;
    border:none;
    background:transparent;
    
    &:hover{
        color: #5f5f5f;
    }

`
const Button = styled(Link)`
position:relative;
text-align:center;
align-self:center;
padding: 20px 75px;
justify-self:center;
text-decoration:none;
outline:none;
background: linear-gradient(180deg, rgba(18,18,18,1) 0%, rgba(0,0,0,1) 100%);
border-radius: 10px;
color:white;
margin: 15px 0;

&:focus,&:hover{
    transform:scale(0.99);
    transition:transform 200ms ease-in-out; 
}`
const Back = styled(Link)`
    text-decoration: none;
    font-weight: bold;
    color:black;
    font-size: 18px;
    text-align:center;
    margin: 20px 0;
`

const JoinScreen = ({ }) => {
    const [gameId, setGameId] = useState('')

    return <Screen>
        <Container>
            <Input onChange={e => setGameId(e.target.value)} placeholder="Voer hier je key in" />
        </Container>
        {gameId.length > 6 && <Button to={`/game/${gameId}/`}>Ga naar de game</Button>}
        <Back to="/">Terug naar menu</Back>
    </Screen>
}
export default JoinScreen;