import React from 'react';
import styled from "styled-components"
import { Link } from "react-router-dom"

const Screen = styled.section`
    display:grid;
    grid-template-columns: 1fr;
    grid-auto-flow:row;
    grid-auto-rows: max-content;
`
const Title = styled.h1`
text-align:center;
align-self:start;
`
const Button = styled(Link)`
    position:relative;
    text-align:center;
    align-self:center;
    padding: 20px 75px;
    text-decoration:none;
    outline:none;
    background: linear-gradient(180deg, rgba(18,18,18,1) 0%, rgba(0,0,0,1) 100%);
    border-radius: 10px;
    color:white;
    margin: 2.5px 0;

    &:focus,&:hover{
        transform:scale(0.99);
        transition:transform 200ms ease-in-out; 
    }
`
const Back = styled(Link)`
    text-decoration: none;
    font-weight: bold;
    color:black;
    font-size: 18px;
    text-align:center;
    margin: 20px 0;
`
const Header = styled.div`
    position:fixed;
    top:0;
    left:0;
    right:0;
    z-index: 5;
    text-align:center;
    padding: 12.5px;
    background: red;
    color:white;
    font-weight: 600;
`
const StartScreen = ({ }) => {
    return <Screen>
        <Header>Waarschuwing: Dit project is nog in de maak. Waarschijnlijk zal het momenteel niet werken!</Header>

        <Title>Lasertag v1</Title>
        <Button to="/create/">Create new game</Button>
        <Button to="/join/">Join game</Button>
        <Back to="/">Terug naar menu</Back>
    </Screen>
}
export default StartScreen;