import React, { useState } from 'react';
import { BrowserRouter, Route, Switch } from "react-router-dom"
//components
import IndexScreen from "./components/index"
import StartScreen from "./components/start"
import JoinScreen from "./components/join"
import OpenSource from "./components/source"
import DocScreen from "./components/doc"
import GameScreen from "./components/game"
import CreateScreen from "./components/create"





function App() {
  const [gameId, setGameId] = useState('A2YzalmBgcRwMSWVedEz');
  return (
    <BrowserRouter >
      <Switch>
        <Route path={["/game/:id", "/game"]} render={route => <GameScreen route={route} gameId={gameId} />} />
        <Route path="/create" render={route => <CreateScreen route={route} setGameId={setGameId} />} />
        <Route path="/start" component={StartScreen} />
        <Route path="/join" component={JoinScreen} />
        <Route path="/opensource" component={OpenSource} />
        <Route path="/doc" component={DocScreen} />
        <Route component={IndexScreen} />
      </Switch>
    </BrowserRouter>
  );
}

export default App;
