import React, { useState, useEffect } from 'react';
import styled from "styled-components"
import { useHistory } from "react-router-dom"
import { db } from "../../../../database"

const Section = styled.section`
justify-self:stretch;
align-self:stretch;
display:flex;
flex-flow: column wrap;;
overflow-y: hidden;
align-items: center;
justify-content: center;
`
const Container = styled.div`
    display:grid;
    max-width: 250px;
    width: calc(100vw - 20px);
    margin: 10px;
    height: 50px;
    border-radius: 10px;
    grid-template-columns: 50px 1fr 50px;
    cursor:pointer;
    background:#fff;
    box-shadow: 0 0 5px 0 #0000001c;
    padding: 7.5px;
    transition:transform 200ms ease-in-out; 
    &:focus,&:hover{
        transform:scale(0.99);
        transition:transform 200ms ease-in-out; 
    }
`
const AddContainer = styled(Container)`
grid-template-columns: 1fr;
background: ${({ color }) => color || "#121212"};
color: white;
`
const Text = styled.h3`
    margin:0;
    padding:0;
    font-size:20px;
`
const Number = styled(Text)`
justify-self:center;
align-self:center;
text-align:center;
`
const Name = styled(Text)`
justify-self:start;
align-self:center;
text-align:left;
`
const Team = styled.div`
justify-self:center;
align-self:center;
text-align:center;
width: 25px;
height: 25px;
border-radius: 25px;
background: ${({ color }) => color}
`


let teamList = ["#65fcb8", "#ff086b"]

const Lobby = ({ gameId, color }) => {
    const [list, setList] = useState([])
    let history = useHistory();

    useEffect(() => {
        db.collection('games').doc(gameId).collection('players').onSnapshot(snap => { setList([]); snap.forEach(doc => setList(e => [...e, { ...doc.data(), id: doc.id }])) });
    }, [gameId])

    return <Section >
        {list.map(({ displayName, team }, i) =>
            <Container key={i}>
                <Number>{`#${i + 1}`}</Number>
                <Name>{displayName}</Name>
                <Team onClick={() => setList(list => list.map((e, j) => j === i ? { displayName, team: team == 0 ? 1 : 0 } : e))} color={teamList[team]} />
            </Container>)}

        <AddContainer color={color} onClick={() => history.push(`add`)}>
            <Number>Voeg speler toe</Number>
        </AddContainer>
    </Section>
}
export default Lobby;