import React, { useState, useEffect } from 'react';
import { rdb, getPlayers } from "../../../../database"
import styled, { keyframes } from "styled-components"
import { useLocation } from "react-router-dom"

const Section = styled.section`
justify-self:center;
align-self:center;
grid-column:1/3;
display:grid;
grid-auto-flow: column;
grid-template-rows: 1fr 1fr;
grid-auto-columns: 1fr;
gap:15px;
`
const Container = styled.div`
justify-self:stretch;
align-self:stretch;
padding: 0 5vw;
display:grid;
grid-template-rows: repeat(2,max-content);
grid-template-columns: 1fr;
row-gap: 2.5px;
`
const Value = styled.h3`
font-size: 20px;
justify-self:start;
align-self:end;
margin:0;
`
const Subject = styled.h4`
font-size: 20px;
justify-self:start;
align-self:start;
margin:0;
`

const PlayerInfo = ({ gameId }) => {
    const [playerIndex, setPlayerIndex] = useState({})
    const [playerData, setPlayerData] = useState({})
    const { search } = useLocation()
    useEffect(() => {

        getPlayers(gameId).then(data => setPlayerIndex(data)) //get player list
        const ref = rdb.ref(`${gameId}/players/${search.split('?')[1]}`)
        search && ref.on('value', (snap) => setPlayerData({ ...snap.val(), key: snap.key }));

    }, [search])
    return <Section>
        {search ? (Object.keys(playerData).map(key => <Container key={key}><Value>{playerData[key]}</Value><Subject>{key}</Subject></Container>)) : <></>}
        {search ? <Container key={'name'}><Value>{playerIndex[search.split('?')[1]] ? playerIndex[search.split('?')[1]].displayName : 'null'}</Value><Subject>Naam</Subject></Container> : <></>}
    </Section>
}
export default PlayerInfo;