import React from 'react';
import styled from "styled-components"
import { Link } from "react-router-dom"

const Screen = styled.section`
    display:grid;
    grid-template-columns: 1fr;
    grid-auto-flow:row;
    grid-auto-rows: max-content;
`
const Title = styled.h1`
text-align:center;
align-self:start;
`
const Button = styled.a`
    position:relative;
    text-align:center;
    align-self:center;
    padding: 20px 75px;
    text-decoration:none;
    outline:none;
    background: linear-gradient(180deg, rgba(18,18,18,1) 0%, rgba(0,0,0,1) 100%);
    border-radius: 10px;
    color:white;
    margin: 2.5px 0;

    &:focus,&:hover{
        transform:scale(0.99);
        transition:transform 200ms ease-in-out; 
    }
`
const Feed = styled.p`
font-size: 18px;
text-align:center;
max-width: 600px;
`
const Back = styled(Link)`
    text-decoration: none;
    font-weight: bold;
    color:black;
    font-size: 18px;
    text-align:center;
    margin: 20px 0;
`

const OpenSource = ({ }) => {
    return <Screen>
        <Title>Source Code</Title>
        <Feed>Dit project wordt gerund door twee scholieren, wij zijn geen professionals en moeten nog veel leren. Stel je lijkt dit een leuk project, dan zouden wij het leuk vinden als je hier aan mee zou werken!</Feed>
        <Button href="https://github.com/jobrav/lasergunv1" target="_">Gun software (C/Arduino)</Button>
        <Button href="https://github.com/jobrav/consolev1" target="_" >Console software (Python)</Button>
        <Button href="https://github.com/jobrav/lasertag-home----backendsoftware" target="_">Game backend software (NodeJS/Firebase)</Button>
        <Back to="/">Terug naar menu</Back>
    </Screen>
}
export default OpenSource;