import React, { useState, useEffect } from 'react';
import styled from "styled-components"
import * as firebase from "firebase";
import { useHistory } from "react-router-dom"
import { db } from "../../../../database"
//components
import FloatContainer from "../floatContainer"

const Section = styled.section`
    width: 100vw;
    height: 100vh;
    max-width: 600px;
    max-height: 400px;
    background:white;
    border-radius:2.5px;

    display:grid;
    grid-template-rows: repeat(3,1fr);
`
const UserType = styled.h3`
justify-self:center;
align-self:center;
font-size:20px;

`
const DisplayName = styled.input`
justify-self:center;
align-self:center;
    margin: 0 25px;
    height: 40px;
    font-size:20px;
    padding-left:5px;
`
const Submit = styled.button`
justify-self:center;
align-self:center;
font-size:20px;
transition:transform 200ms ease-in-out; 
&:focus,&:hover{
    transform:scale(0.99);
    transition:transform 200ms ease-in-out; 
}
`
const setPendingPlayer = firebase.functions().httpsCallable('setPendingPlayer');

const AddPlayer = ({ gameId }) => {
    const [displayName, setDisplayName] = useState('')
    const [pending, setPending] = useState(false);
    let history = useHistory();

    const submitProfile = () => {
        if (displayName.length > 3) {
            setPending(true)
            const uid = `${displayName}#id123`;
            setPendingPlayer({ uid, displayName, gameKey: gameId }).then(_ => {
                let unsub = db.collection('games').doc(gameId).collection('players').doc(uid).onSnapshot(({ exists, id }) => {
                    if (exists && id === uid) {
                        unsub();
                        history.push(`/game/${gameId}/`)
                    }
                });

            }).catch(err => { console.log("submitProfile:", err); setPending(false) })
        }
    }

    return <FloatContainer>
        {pending ?
            <Section>
                <UserType>Loading</UserType>
                <UserType>{displayName}</UserType>
            </Section>
            :
            <Section>
                <UserType>Speel zonder acount</UserType>
                <DisplayName placeholder="Jou naam" onChange={({ target: { value } }) => setDisplayName(value)} />
                <Submit onClick={submitProfile}>Verbinden</Submit>
            </Section>}
    </FloatContainer>
}
export default AddPlayer;