import React, { useState, useEffect } from 'react';
import { rdb, getPlayers } from "../../../../database"
import styled from "styled-components"
import { Link } from "react-router-dom"

const Section = styled.section`
justify-self:stretch;
align-self:stretch;
display:grid;
grid-auto-flow: row;
grid-auto-rows: max-content;
grid-template-columns: 1fr;
row-gap: 5px;
// padding-top: 15px;
`
const Container = styled.div`
justify-self:end;
align-self:stretch;
display:grid;
grid-template-columns: max-content auto max-content;
grid-template-rows: 1fr;
column-gap: 5px;
background: #000;
border-radius: 2.5px;
transition:transform 200ms ease-in-out; 
&:hover{
    transform:scale(1.01);
}
`
const Name = styled(Link)`
text-decoration:none;
    padding: 2.5px 10px;
    border-radius: 2.5px;

    color:white;
    font-size: 30px;
    justify-self:center;
    align-self:center;
    cursor:pointer;
    transition:all 200ms ease-in-out;
    &:hover{
        background: #272727;
    }
`
const Arrow = styled.svg`
fill:white;
width: 25px;
justify-self:center;
align-self:center;
`

const History = ({ gameId }) => {
    const [playerIndex, setPlayerIndex] = useState({})
    const [historyFeed, setHistoryFeed] = useState([])

    useEffect(() => {
        const ref = rdb.ref(`${gameId}/history`).orderByChild('timestamp').limitToLast(5);
        ref.on('value', (snap) => {
            setHistoryFeed([])
            snap.forEach(data => setHistoryFeed(e => [...e, data.val()]))
        });


        getPlayers(gameId).then(data => setPlayerIndex(data)) //get player list
    }, [gameId])

    const laserGunImg = <g>
        <path d="M115.5,48.955a7.758,7.758,0,0,0-7.542,6H98.884A14.4,14.4,0,0,0,88.75,42.882v-3.3A1.75,1.75,0,0,0,87,37.83H51.973c-2.3-6.838-9.487-14.322-15.031-15.36a1.75,1.75,0,0,0-2.012,2.173,33.3,33.3,0,0,1,.93,13.187H29.17a13.7,13.7,0,0,0-9.6,3.92H18.5A13.766,13.766,0,0,0,4.75,55.5v2.411A13.766,13.766,0,0,0,18.5,71.661h1.075a13.7,13.7,0,0,0,9.6,3.919h1.722L24.87,90.9a10.719,10.719,0,0,0-.74,3.916,5.771,5.771,0,0,0,1.113,3.437,5.858,5.858,0,0,0,2.94,2.129l15.78,5.09a1.75,1.75,0,0,0,2.244-2.051l-.85-3.761a35.489,35.489,0,0,1-.551-12.624H47.54A13.688,13.688,0,0,0,61.083,75.58H87a1.749,1.749,0,0,0,1.75-1.75v-3.3A14.473,14.473,0,0,0,98.881,58.451h9.074a7.747,7.747,0,1,0,7.545-9.5ZM39.058,27.074A25.422,25.422,0,0,1,48.231,37.83h-8.84A35.421,35.421,0,0,0,39.058,27.074ZM8.25,57.911V55.5a10.257,10.257,0,0,1,8.661-10.114A13.649,13.649,0,0,0,15.42,51.58V61.83a13.642,13.642,0,0,0,1.491,6.194A10.255,10.255,0,0,1,8.25,57.911ZM18.92,61.83V51.58A10.262,10.262,0,0,1,29.17,41.33h8.168a.074.074,0,0,0,.021,0H79.25V72.08H29.17A10.261,10.261,0,0,1,18.92,61.83ZM39.8,100.454l-.418-1.84c-.212-.936-.388-1.888-.522-2.829a1.75,1.75,0,1,0-3.464.494c.138.973.32,1.956.535,2.927l-6.658-2.148a2.374,2.374,0,0,1-1.2-.864,2.3,2.3,0,0,1-.44-1.374,7.219,7.219,0,0,1,.5-2.639l6.524-16.6h3.431l-.018.041a39.507,39.507,0,0,0-2.794,10.506,1.751,1.751,0,0,0,3.475.426,35.89,35.89,0,0,1,2.543-9.568l.6-1.405h2.621l-.459,1.085A38.413,38.413,0,0,0,41.619,84.8c-.01.036-.017.073-.025.11,0,.014-.007.028-.01.043l-.008.032a39,39,0,0,0,.367,15.45l.174.767ZM47.54,83.54H45.463a34.691,34.691,0,0,1,1.808-5.507L48.31,75.58h9.214A10.2,10.2,0,0,1,47.54,83.54ZM82.75,72.08V41.33h2.5V72.08Zm6-5.265V46.591a10.949,10.949,0,0,1,0,20.224Zm26.75-5.86a4.25,4.25,0,1,1,4.25-4.25A4.254,4.254,0,0,1,115.5,60.955Z" /><path d="M31.583,48.955a1.75,1.75,0,0,0-1.75,1.75v12a1.75,1.75,0,0,0,3.5,0v-12A1.75,1.75,0,0,0,31.583,48.955Z" /><path d="M41.667,62.705v-12a1.75,1.75,0,0,0-3.5,0v12a1.75,1.75,0,0,0,3.5,0Z" /><path d="M48.25,64.455A1.749,1.749,0,0,0,50,62.705v-12a1.75,1.75,0,0,0-3.5,0v12A1.749,1.749,0,0,0,48.25,64.455Z" />
    </g>
    return <Section >
        {historyFeed.map(({ assassinId, victimId }, i) => <Container key={i}>
            <Name to={{ pathname: 'player', search: assassinId }}> {`${playerIndex[assassinId] ? playerIndex[assassinId].displayName : 'undefined'}`}</Name>
            <Arrow xmlns="http://www.w3.org/2000/svg" viewBox="0 0 128 128">{laserGunImg}</Arrow>
            <Name to={{ pathname: 'player', search: victimId }}>{` ${playerIndex[victimId] ? playerIndex[victimId].displayName : 'undefined'}`}</Name></Container>)}
    </Section>
}
export default History;